.song{
  font-weight: bold;
  -webkit-animation: scroll 20s linear infinite;
          animation: scroll 20s linear infinite;
}

@-webkit-keyframes scroll {
  0% {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
  }
  100% {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
  }
}

@keyframes scroll {
  0% {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
  }
  100% {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
  }
}

.progress{
  /* position: sticky; */
  height: 1em;
  width: 100;
  bottom: 0em;
  align-items: center;
  justify-content: center;
  }


@media (max-width: 685px) {

  .display-container{
    top: var(--nav-size);
    width: 100%;
    left: 0%;
    height: calc(var(--nav-size)*2);
  }
  .display{
    width: 290px;
    padding: auto;
    height: 90px;
    flex-direction: column-reverse;
    padding-left: 0;
    padding-right: 0;
    
  }
  .display-left{
    height: 40%;
    width: 100%;
  }
  .display-right{
    height: 60%;
    width: 300px;
  }
  .progress{
    -webkit-transform: translateX(6%);
            transform: translateX(6%);
    bottom: 2em;
    width: 90%;
  }
  .screen{
    width: 260px;
  }
}



  
.prompt{
    position: fixed;
    display: flex;
    top: 50%;
    left: 50%;
    align-items: center;
    justify-content: center;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    z-index: inherit;
  }

.spinner{
  position: fixed;
  display: flex;
  /* top: 50%; */
  left: 37.5%;
  align-items: center;
  justify-content: center;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -webkit-animation: spin 3s infinite linear;
          animation: spin 3s infinite linear;
}

.center-container{
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 300px;
  z-index: 1; 
}

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);;
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);;
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

